import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Icon } from '@cetus/ui-kit'
import { Box, HStack, Popover, PopoverBody, PopoverContent, PopoverTrigger, StackProps, Text } from '@chakra-ui/react'
import React from 'react'

function WithTooltipInfo({
  label,
  tooltip,
  children,
  wrapStyle = {}
}: {
  label: string
  tooltip: string
  children: React.ReactNode
  wrapStyle?: StackProps
}) {
  const { isApp } = useWindowWidth()

  return (
    <HStack w="100%" justify="space-between" {...wrapStyle}>
      <HStack gap="4px">
        <Text>{label}</Text>
        <Popover isLazy trigger={isApp ? 'click' : 'hover'}>
          <PopoverTrigger>
            <Box>
              <Icon xlinkHref="#icon-icon_tips" />
            </Box>
          </PopoverTrigger>
          <PopoverContent>
            <PopoverBody p="12px" fontSize="12px" lineHeight="20px">
              {tooltip}
            </PopoverBody>
          </PopoverContent>
        </Popover>
      </HStack>
      {children}
    </HStack>
  )
}

export default WithTooltipInfo
